"use client";

import type { Metadata } from "next";
import { Inter } from "next/font/google";
import "./globals.css";
import { useEffect } from "react";

const inter = Inter({ subsets: ["latin"] });

export const metadata: Metadata = {
  title: "Vinyl Record Cataloging App",
  description: "Catalog your vinyl record collection",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  // Suprime erros de hidratação causados por extensões de navegador
  useEffect(() => {
    // Este efeito só é executado no cliente
    const originalError = console.error;
    console.error = (...args) => {
      if (
        typeof args[0] === 'string' && 
        args[0].includes('Warning: Text content did not match') ||
        args[0].includes('Warning: Prop') && args[0].includes('did not match') ||
        args[0].includes('A tree hydrated but some attributes')
      ) {
        // Ignora erros de hidratação específicos
        return;
      }
      originalError(...args);
    };

    return () => {
      console.error = originalError;
    };
  }, []);

  return (
    <html lang="en">
      <body className={inter.className}>{children}</body>
    </html>
  );
}
